import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import type { UseMutationResult } from 'react-query'
import { useMutation, useQuery, useQueryClient } from 'react-query'

import { APPLY_MESSAGES, CLUB_FORMS_MESSAGE, COMMON_MESSAGE } from 'src/data/messages'

import type { Applicant, SubmitApplicant } from 'src/models/applicants'
import type { ClubId } from 'src/models/club'

// Admins view, allow applicants

interface GetApplicantsParameters {
  clubId: ClubId
  recruitId: number | undefined
  token: string
}

const getApplicantsAPICall =
  ({ clubId, recruitId, token }: GetApplicantsParameters) =>
  async () => {
    const response = await axios.get<Applicant[]>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}/applicants`,
      {
        headers: { Authorization: token }
      }
    )

    return response.data.sort((it1,it2) => it1.id - it2.id)
  }

export const useGetApplicants = (clubId: ClubId, recruitId?: number) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<Applicant[]>('applicants', getApplicantsAPICall({ clubId, recruitId, token }), {
    onError: error => {
      if (!(error instanceof AxiosError)) {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    },
    enabled: !!recruitId && !!token
  })
}

export type PutApplicantsParams = {
  clubId: ClubId
  recruitId: number
  data: SubmitApplicant[]
}

const putApplicantsAPICall = async ({ clubId, data, recruitId }: PutApplicantsParams, token: string) => {
  await axios.put<null, AxiosResponse<null>, SubmitApplicant[]>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}/results`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}

export const useAllowApplicant = (): UseMutationResult<void, unknown, PutApplicantsParams, void> => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  const mutationFn = (values: PutApplicantsParams) => putApplicantsAPICall(values, token)

  return useMutation('putApplicant', mutationFn, {
    onSuccess: () => {
      enqueueSnackbar(APPLY_MESSAGES.SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('applicants')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        enqueueSnackbar(error.response?.data?.message ?? CLUB_FORMS_MESSAGE.EDIT_FAIL, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
