import { useState } from 'react'
import { useNavigate } from 'react-router'

import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import { Box, Button, Card, Chip, Typography, colors, useMediaQuery, useTheme } from '@mui/material'
import type { GridColDef, GridEventListener, GridRowSelectionModel } from '@mui/x-data-grid'
import { DataGrid } from '@mui/x-data-grid'

import formatDateTime from 'src/utils/formatDateTime'

import { type Applicant } from 'src/models/applicants'

import BulkActions from 'src/content/manage/Recruit/Applicants/BulkActions'

interface ApplicantDatagridProps {
  applicants: Applicant[]
  handleRowSelect: GridEventListener<'rowClick'>
  clubId: number
  recruitId: number
}

const columns: GridColDef[] = [
  {
    field: 'name',
    headerName: '이름',
    width: 100
  },
  {
    field: 'studentId',
    headerName: '학번',
    width: 150
  },
  {
    field: 'department',
    headerName: '소속 학부/학과',
    width: 200
  },
  {
    field: 'phoneNumber',
    headerName: '연락처',
    width: 150
  },
  {
    field: 'email',
    headerName: '이메일',
    width: 150
  },
  {
    field: 'result',
    headerName: '합격',
    width: 100,
    renderCell: params => {
      let statusColor: string = colors.grey[800]
      let bgColor: string = colors.grey[100]
      switch (params.value) {
        case 'FAIL':
          statusColor = colors.red[800]
          bgColor = colors.red[50]
          break
        case 'PASS':
          statusColor = colors.green[800]
          bgColor = colors.green[50]
          break
      }
      return <Chip label={params.value} size="small" sx={{ color: statusColor, bgcolor: bgColor }} />
    }
  },
  {
    field: 'appliedAt',
    headerName: '지원 시각',
    width: 150,
    renderCell: params => {
      const date = new Date(params.value)
      return <Typography>{formatDateTime(date)}</Typography>
    }
  }
]

const ApplicantDatagrid = ({ applicants, handleRowSelect, clubId, recruitId }: ApplicantDatagridProps) => {
  const navigate = useNavigate()
  const theme = useTheme()
  const isPc = useMediaQuery(theme.breakpoints.up('md'))

  const [selectedApplicantsIds, setSelectedApplicantsIds] = useState<GridRowSelectionModel>([])

  const handleRowSelectionChange = (selectedMemberIds: GridRowSelectionModel) => {
    setSelectedApplicantsIds(selectedMemberIds)
  }

  return (
    <Card sx={{ minHeight: '100%', width: '100%' }}>
      {selectedApplicantsIds.length ? (
        <Box sx={{ display: 'inline-block', paddingY: 1, width: 'auto' }}>
          <BulkActions selectedApplicants={selectedApplicantsIds.map(Number)} clubId={clubId} recruitId={recruitId} />
        </Box>
      ) : (
        <Box sx={{ display: isPc ? 'flex' : 'block', pt: 1, alignItems: 'center' }}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Button
              variant="text"
              onClick={() => {
                navigate(-1)
              }}
              color="primary"
            >
              <ArrowBackIcon />
            </Button>
            <Box>
              <Typography variant="h4" display={isPc ? 'inline' : 'block'}>
                지원자 목록&nbsp;&nbsp;
              </Typography>
              <Typography variant="caption" display={isPc ? 'inline' : 'block'}>
                합격 시 준회원으로 등록됩니다
              </Typography>
            </Box>
          </Box>
        </Box>
      )}
      {applicants.length ? (
        <Box sx={{ height: '500px', width: '100%' }}>
          <DataGrid
            rows={applicants}
            columns={columns}
            checkboxSelection
            onRowClick={handleRowSelect}
            disableRowSelectionOnClick
            rowSelectionModel={selectedApplicantsIds}
            onRowSelectionModelChange={handleRowSelectionChange}
          />
        </Box>
      ) : (
        <Typography my={10} textAlign={'center'}>
          아직 지원자가 없어요!
        </Typography>
      )}
    </Card>
  )
}

export default ApplicantDatagrid
