import { useState } from 'react'

import CheckCircleTwoToneIcon from '@mui/icons-material/CheckCircleTwoTone'
import PersonOffTwoToneIcon from '@mui/icons-material/PersonOffTwoTone'
import { Box, Button, Typography } from '@mui/material'

import type { ResultType, SubmitApplicant } from 'src/models/applicants'
import type { ClubId } from 'src/models/club'

import ConfirmDialog from 'src/components/ConfirmDialog'

import { useAllowApplicant } from 'src/hooks/api/applicants'

const PASS = 'PASS'
const FAIL = 'FAIL'
interface BulkActionsProps {
  selectedApplicants: number[]
  onCloseModal?: () => void
  clubId: ClubId
  recruitId: number
  currentResultType?: ResultType
  handleCloseForm?: () => void
}

function BulkActions({
  selectedApplicants,
  onCloseModal,
  clubId,
  recruitId,
  currentResultType = 'WAIT',
  handleCloseForm
}: BulkActionsProps) {
  const [isDialog, setIsDialog] = useState<boolean>(false)
  const [resultType, setResultType] = useState<ResultType>(FAIL)

  const allowApplicants = useAllowApplicant()

  const isAlreadyPass = currentResultType === PASS
  const isAlreadyFail = currentResultType === FAIL

  const dialogMessage =
    isAlreadyFail || isAlreadyPass
      ? `심사 상태를 ${resultType === FAIL ? '불' : ''}합격으로 변경하시겠습니까?`
      : `정말 ${resultType === FAIL ? '불' : ''}합격처리 하시겠습니까?`

  const handleFail = () => {
    setResultType(FAIL)
    setIsDialog(true)
  }

  const handlePass = () => {
    setResultType(PASS)
    setIsDialog(true)
  }

  const handleClick = (resultType: ResultType) => {
    const submitApplicants = selectedApplicants.map(id => {
      return {
        userId: id,
        result: resultType as ResultType
      }
    })
    handleSubmit(submitApplicants)
    setIsDialog(false)
  }

  const handleSubmit = async (submitApplicants: SubmitApplicant[]) => {
    allowApplicants.mutate({ clubId, recruitId, data: submitApplicants })
    onCloseModal && onCloseModal()
    handleCloseForm && handleCloseForm()
  }

  return (
    <>
      <Box>
        <Typography variant="h6" textAlign={'center'} mb={1}>
          {isAlreadyFail && '불합격처리됨'}
          {isAlreadyPass && '합격처리됨'}
        </Typography>

        <Box display="flex" alignItems="center" justifyContent="space-between">
          {!isAlreadyFail && (
            <Button
              sx={{ ml: 1 }}
              startIcon={<PersonOffTwoToneIcon />}
              variant={'contained'}
              onClick={handleFail}
              color="error"
            >
              {isAlreadyPass ? '불합격으로 전환' : '불합격'}
            </Button>
          )}
          {!isAlreadyPass && (
            <Button
              sx={{ ml: 1 }}
              startIcon={<CheckCircleTwoToneIcon />}
              variant={'contained'}
              onClick={handlePass}
              color={'success'}
            >
              {isAlreadyFail ? '합격으로 전환' : '합격'}
            </Button>
          )}
        </Box>
      </Box>
      <ConfirmDialog
        open={isDialog}
        handleClose={() => {
          setIsDialog(false)
        }}
        handleSubmit={() => {
          handleClick(resultType)
        }}
        confirmMessage={dialogMessage}
      />
    </>
  )
}

export default BulkActions
