import { useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router-dom'

import { Container, Dialog, Grid, useMediaQuery, useTheme } from '@mui/material'
import type { GridEventListener } from '@mui/x-data-grid'

import type { Applicant } from 'src/models/applicants'

import PageTitleWrapper from 'src/components/PageTitleWrapper'

import PageHeader from 'src/content/manage/PageHeader'
import ApplicantForm from 'src/content/manage/Recruit/Applicants/ApplicantForm'
import ApplicantDatagrid from 'src/content/manage/Recruit/Applicants/ApplicantsDatagrid'

import { useGetApplicants } from 'src/hooks/api/applicants'
import { useGetRecruits } from 'src/hooks/api/recruit'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

const ApplicantsPage: React.FC = () => {
  const { id, recruitId } = useParams()
  const { userInfo } = useSafeAuthUser()
  const clubId = Number(id)
  const club = userInfo?.clubs.find(club => club.id === clubId)

  const { data: recruit, isSuccess: isRecruitSuccess } = useGetRecruits(clubId)
  const { data: applicants, isSuccess: isApplicantSuccess } = useGetApplicants(clubId, Number(recruitId))

  const theme = useTheme()
  const isPc = useMediaQuery(theme.breakpoints.up('md'))

  const [isOpenForm, setIsOpenVisible] = useState(false)
  const [visibleApplicant, setVisibleApplicant] = useState<Applicant | null>(null)

  const handleCloseForm = () => {
    setIsOpenVisible(false)
  }

  const handleRowSelect: GridEventListener<'rowClick'> = params => {
    setIsOpenVisible(true)
    setVisibleApplicant(params.row)
  }

  return (
    <>
      <Helmet>
        <title>지원자 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} />
      </PageTitleWrapper>
      <Container
        sx={{
          height: '100%',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'end',
          alignItems: 'center'
        }}
      >
        {isRecruitSuccess && isApplicantSuccess && (
          <Grid
            container
            height="100%"
            direction="row"
            alignItems="stretch"
            columnSpacing={2}
            justifyContent={'center'}
            width={'100%'}
          >
            <Grid item xs={isPc && isOpenForm ? 8 : 12} height="100%">
              <ApplicantDatagrid
                clubId={clubId}
                recruitId={Number(recruitId)}
                applicants={applicants}
                handleRowSelect={handleRowSelect}
              />
            </Grid>
            {applicants.length !== 0 &&
              isOpenForm &&
              (isPc ? (
                <Grid item xs={4} height="100%">
                  <ApplicantForm
                    clubId={clubId}
                    recruitId={Number(recruitId)}
                    visibleApplicant={visibleApplicant}
                    handleCloseForm={handleCloseForm}
                  />
                </Grid>
              ) : (
                <Dialog open={isOpenForm} onClose={handleCloseForm} maxWidth="sm">
                  <ApplicantForm
                    clubId={clubId}
                    recruitId={recruit.id}
                    visibleApplicant={visibleApplicant}
                    handleCloseForm={handleCloseForm}
                  />
                </Dialog>
              ))}
          </Grid>
        )}
      </Container>
    </>
  )
}

export default ApplicantsPage
